// 获取common.js中的服务器地址
//import common from "../../common/common.js";

//导入自定义组件
import trailerStars from "../../components/trailerStars.vue"

export default {
	data() {
		return {
			carouselList: [],
			hotSuperHeroList: [],
			hotTrailerList: [],
			animationData: {}
		}
	},
	onUnload() {
		// 页面卸载的时候，清楚动画对象
		this.animationData = {};
	},
	onLoad() {
		var me = this;
		animationData


		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();


		// 获取common.js中的服务器地址
		//var serverUrl = common.serverUrl;
		// 通过挂载到main.js中获取服务器的地址，作为全局变量
		var serverUrl = me.serverUrl;

		// 请求轮播图数据
		uni.request({
			url: serverUrl + '/index/Carousel',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var carouselList = res.data;
					me.carouselList = carouselList;
				}
			}
		});

		// 查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot?type=superhero',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var hotSuperHeroList = res.data;
					me.hotSuperHeroList = hotSuperHeroList;
				}
			}
		});

		// 查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot?type=trailer',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var hotTrailerList = res.data;
					me.hotTrailerList = hotTrailerList;
				}
			}
		});
	},
	methods: {
		// 实现动画点赞效果
		praiseMe() {
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});

			// 导出动画数据到view组件，实现组件的动画效果
			this.animationData = this.animation.export();

			// 还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				})
			}.bind(this), 500);
		}
	},

	components: {
		trailerStars
	}
}
